.c-pageHeader {
    position: relative;
    overflow: hidden;
    padding-top: 8px;
    background: var(--color-cerulean);

    .wrapper {
        display: flex;
        align-items: center;

        @include respond-to('medium') {
            display: block;
        }
    }
    &::before {
        content: '';
        position: absolute;
        top: 0; 
        right: 0;
        width: 46px;
        height: 100%;
        background: url('../img/sea_background.webp') no-repeat bottom left;
    }
    &-image {
        position: relative;
        width: 50%;
        min-height: 510px;
        
        img {
            position: absolute;
            top: 0;
            right: 0;
            width: 50vw;
            height: 100%;
            max-width: none;
            border-radius: 0 370px 0 0;
            object-fit: cover;
        }
        @include respond-to('medium') {
            width: 100%;
            min-height: 0;
            height: 325px;
            background: #fff;

            img {
                position: static;
                width: 100%;
                height: 100%;
            }
        }
    }
    &-text {
        position: relative;
        color: #fff;
        width: 50%;
        padding-left: 50px;
        padding-right: 60px;

        &-pretitle {
            display: inline-block;
            position: relative;
            @include rhythm(14, 18);
            font-weight: 700;
            color: var(--color-primary);
            text-transform: uppercase;
            margin-bottom: 10px;

            span {
                position: relative;
            }
            &::before {
                content: '';
                position: absolute;
                z-index: 0;
                top: 3px;
                left: -4px;
                background: var(--color-secondary);
                width: calc(100% + 24px);
                height: 8px;
            }
        }
        &-title {
            position: relative;
            @include rhythm(40, 44);
            margin: 0 0 10px;
            letter-spacing: 0.4px;

            &::before {
                content: '';
                position: absolute;
                top: 20px;
                right: calc(100% + 14px);
                width: 200px;
                height: 4px;
                background: var(--color-secondary);
                border-radius: 10px;

                @include respond-to('medium') {
                    top: -51px;
                    right: calc(50% - 2px);
                    width: 4px;
                    height: 42px;
                }
            }
            @include respond-to('medium') {
                position: static;
            }
        }
        p {
            @include rhythm(16, 20);
            margin: 10px 0;
        }
        @include respond-to('medium') {
            width: 100%;
            margin-top: 30px;
            padding-bottom: 30px;
            padding-left: var(--hspace);
        }
    }
    &[data-background="primary"] {
        background: var(--color-primary);
    }
    &[data-background="secondary"],
    &[data-background="white"] {
        .c-pageHeader-text {
            &-title {
                color: var(--color-cerulean);
            }
            p {
                color: var(--color-primary);
            }
        }
    }
    &[data-background="secondary"] {
        background: #cffff2;
    }
    &[data-background="white"] {
        background: #fff;
    }

    @include respond-to(medium) {
        padding-top: 0;
    }
}